import React, {memo, useCallback} from 'react'
import styled from 'styled-components'
import {colors} from 'app/styles'
import {useInputNumber} from 'app/hooks'
import {inputStyle} from 'app/components/Input/constants'

const BaseInput = styled.input`
  ${inputStyle}
`

export const Input = memo(({inputRef, component: Component = BaseInput, ...props}) => (
  <Component ref={inputRef} {...props} />
))

export const InputNumber = memo(props => {
  const inputNumberProps = useInputNumber(props)

  const handleChange = useCallback(e => inputNumberProps.onChange(e.target.value), [])

  return (
    <Input {...props} {...inputNumberProps} onChange={handleChange}/>
  )
})

export const DashedInput = styled(Input)`
  padding: 0;
  height: 20px;
  border-radius: 0;
  border: 1px dotted ${colors.inputBorder};

  @media print {
    border: none;
  }
`

export const LabelInput = styled(DashedInput)`
  font-weight: 700;
`
