import React from 'react'
import styled from 'styled-components'
import {useStoreRehydrated, useStoreState} from 'easy-peasy'
import {useDarkTheme, useManifestTheme, usePrimaryColor} from 'app/hooks'
import {Header} from 'app/components/Header'
import {Sidebar} from 'app/components/Sidebar'
import {metrics} from 'app/styles'

const Container = styled.div`
  display: flex;
  flex: auto;
  flex-direction: column;
  min-height: 0;
`

const Content = styled.div`
  flex: auto;
  min-height: 0;
  padding-top: ${metrics.headerHeight}px;
  
  @media print {
    padding-top: 0;
  }
`

// TODO refactor styles
/*.container {
  min-height: 100vh;
}

.contentWrapper {
  min-height: 100%;
}*/

export const Layout = ({children}) => {
  const darkTheme = useStoreState(state => state.settings.darkTheme)
  const primaryColor = useStoreState(state => state.settings.primaryColor)
  usePrimaryColor(primaryColor)
  useDarkTheme(darkTheme)
  useManifestTheme(darkTheme, primaryColor)
  // TODO move it higher
  const rehydrated = useStoreRehydrated()

  if (!rehydrated) {
    return null
  }

  return (
    <Container>
      <Header/>
      <Container>
        <Sidebar/>
        <Content>
          {children}
        </Content>
      </Container>
    </Container>
  )
}
